@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 100%;
}

.GameField{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 50px;
    margin: 50px 0px;
    font-family: 'Bebas Neue', 'cursive';
}

.RestartButton {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 80px;
    height: 80px;
    margin: 30px;
    border-radius: 5px;
    border: 3px solid black;
    background-color:rgb(250, 200, 50);
    font-size: 40px;
    font-weight: 900;
}
.Row{
    display: flex;
}

.Tile{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 80px;
    height: 80px;
    font-family: 'Bebas Neue', 'cursive';
    border: solid 1px black;
    background-color: hsl(0, 0%, 94%);
} 

.Tile.Animatied {
    z-index: 1;
}
.Tile.Animatied p{
    opacity: 0;
    transition: left 0.3s linear, top 0.3s linear, opacity 0.2s;
}

.Tile p{
    font-size: 40px;
    top: 0px;
    opacity:  1;
}
.Tile.Animatied.up p{
    top: -40px;
    left: 25px;
}

.Tile.Animatied.down p{
    top: 50px;
    left: 25px;
}

.Tile.Animatied.left p{
    top: 0px;
    left: -45px;
}

.Tile.Animatied.right p{
    top: 0px;
    left: 45px;
}
.GameOverScr{
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    background-color: black;
    opacity: 20%;
    z-index: 256;
}

.GameOverMenu{
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 200px;
    height: 100px;
    font-size: 20px;
    background-color: wheat;
    z-index: 255;
}
.StartScr{
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Bebas Neue', 'cursive';
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.4);
    z-index: 255;
}

.StartMenu{
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 200px;
    height: 100px;
    border-radius: 5px;
    font-size: 30px;
    font-weight: 500;
    background-color:rgb(250, 240, 100);
    z-index: 256;
}

.StartButton {
    border-radius: 5px;
    border: 5px solid black;
    background-color:rgb(250, 200, 50);
    font-size: 25px;
    margin: 10px;
}
